import './Sidebar.css';
import { sideBarLinks } from "../data/admin"
import SidebarLinks from './admin/subComponents/SidebarLinks';
import { AiFillApple } from 'react-icons/ai';

const Sidebar = ({ setIsSideBarOpen, isSideBarOpen }) => {
    return (
        <div className={`admin-dashboard__sidebar ${isSideBarOpen && "opensidebar"}`}>
            <div className="sidebar__header">
                <div className="header_logo">
                    <div className="logo__icon">
                        <AiFillApple />
                    </div>
                    <h2 className="title">Admin Dashboard</h2>
                </div>
                <div onClick={() => setIsSideBarOpen(false)} className="header__close">
                    <span></span>
                    <span></span>
                </div>
            </div>
            <ul className="sidebar__link">
                {sideBarLinks.map((link, index) => {
                    return <SidebarLinks key={index} {...link} />
                })}
            </ul>
        </div>
    )
}

export default Sidebar
