import './Navbar.css'
import { FaBars } from "react-icons/fa";
import { FaUserAlt } from "react-icons/fa";
import { BiSearchAlt2 } from "react-icons/bi";

const Navbar = ({ setIsSideBarOpen, setOpenAdminDashboard }) => {
    const handleShow = () => {
        setIsSideBarOpen(true)
        setOpenAdminDashboard(prev => !prev)
    }
    return (
        <div className="admin-dashboard__navbar">
            <nav className="admin-navbar">
                <div className="navbar__bars">
                    <FaBars onClick={handleShow} />
                </div>
                <div className="navbar__search">
                    <input type="text" placeholder="Seacrh here" />
                    <BiSearchAlt2 className="search-icon" />
                </div>
                <div className="navbar__user-icon">
                    <FaUserAlt />
                </div>
            </nav>
        </div>
    )
}

export default Navbar
