import './BusinessInformation.css'

const BusinessInformation = ({ information }) => {
    const { info, value, icon } = information
    return (
        <div className="buisness-information__card">
            <div className="card__content">
                <h2 className="content__value">{value}</h2>
                <h5 className="content__info">{info}</h5>
            </div>
            <span className="content__icon">
                {icon}
            </span>
        </div>
    )
}

export default BusinessInformation
