import './RecentOrders.css'

const RecentOrders = ({name, price, isPaid, status}) => {

    const statusColor = (status) => {
        let className;
        switch (status) {
            case 'Return':
                className = 'red'
                break;
            case 'Delivered':
                className = 'green'
                break;
            case 'Panding':
                className = 'yellow'
                break;
            case 'In Progress':
                className = 'blue'
                break
            default:
                className = '';
        }
        return className
    }

    return (
        <div className="recent-orders-items recent-orders-items-infos">
            <h5>{name}</h5>
            <h5>${price}</h5>
            <h5>{isPaid ? 'Paid' : 'Due'}</h5>
            <h5 className={`status ${statusColor(status)}`}>
                <span>{status}</span>
            </h5>
        </div>
    )
}

export default RecentOrders