import './RecentCustomers.css'

const RecentCustomers = ({ name, country, picture }) => {
    return (
        <div className="recent-customers-item">
            <div className="recent-customers-item-icon">
                {picture}
            </div>
            <div className="recent-customers-item-infos">
                <h3>{name}</h3>
                <h5>{country}</h5>
            </div>
        </div>
    )
}

export default RecentCustomers
