import './AdminDashboardInfos.css';
import { businessInformation, recentOrders, recentCustomers } from "../../data/admin"
import BusinessInformation from './subComponents/BusinessInformation';
import RecentCustomers from './subComponents/RecentCustomers';
import RecentOrders from './subComponents/RecentOrders';
import { Link } from 'react-router-dom';

const recentOrdersTilte = Object.keys(recentOrders[0])

const AdminDashboardInfos = () => {
    return (
        <>
            <div className="business-information">
                {businessInformation.map((singleInformation, index) => {
                    return <BusinessInformation key={index} information={singleInformation} />
                })}
            </div>
            <div className="infos-content">
            <div className="recent-orders-section">
                <div className="recent-order-header">
                    <h2 className="title">Recent Orders</h2>
                    <Link to="#" className="view-all-recent-orders-link">View All</Link>
                </div>
                <div className="recent-orders">
                    <div className="recent-orders-items recent-orders-title">
                        {recentOrdersTilte.map((title, index) => {
                            return <h4 key={index}>
                                {
                                    title === 'isPaid'
                                        ? 'Payment'
                                        : title
                                }
                            </h4>
                        })}
                    </div>
                    {recentOrders.map((order, index) => {
                        return <RecentOrders key={index} {...order} />
                    })}
                </div>
            </div>
            <div className="recent-custumers">
                <h2 className="title">Recent Customers</h2>
                {recentCustomers.map((customer, index) => {
                    return <RecentCustomers key={index} {...customer} />
                })}
            </div>
            </div>
        </>
    )
}

export default AdminDashboardInfos
