import { useState, useLayoutEffect } from 'react';

import Navbar from '../components/admin/Navbar';
import AdminDashboardInfos  from '../components/admin/AdminDashboardInfos';
import Sidebar from '../components/Sidebar';

const AdminDashboard = ({ openAdminDashboard, setOpenAdminDashboard }) => {

    const [isSideBarOpen, setIsSideBarOpen] = useState(false)

    useLayoutEffect(() => {
        const hideScrollBar = () => {
            if (isSideBarOpen) {
                document.body.style.overflow = 'hidden'
            } else {
                document.body.style.overflow = 'visible'
            }
        }
        hideScrollBar()
    }, [isSideBarOpen])

    return (
       <> 
            <div className={`admin-dashboard ${isSideBarOpen && "opensidebar"} ${openAdminDashboard && "openadmindashboard"}`}>
                <Navbar setOpenAdminDashboard={setOpenAdminDashboard} setIsSideBarOpen={setIsSideBarOpen} /> 
                <div className="admin-dashboard-container">
                    <div className="container">
                        <AdminDashboardInfos />
                    </div>
                </div>
            </div>
            <Sidebar isSideBarOpen={isSideBarOpen} setIsSideBarOpen={setIsSideBarOpen} />
        </>
    )
}

export default AdminDashboard
